<template>
  <div>
    <el-input type="textarea" v-model="content" :rows="3"></el-input>
    <el-button @click="postComment">发表</el-button>
  </div>


</template>

<script>
import axios from "axios";

export default {
  name: "postComment",
  props:{
    bId:Number,
    token:String
  },
  data(){
    return{
      content:''
    }
  },
  methods:{
    postComment(){
      axios.post('/comment/do',{bId:this.bId,comContent:this.content},{
        headers:{Token:this.token}
      }).then(res=>{
        if(res.status>=200 && res.status<400)
          this.$message({
            message:'success',
            type:"success"
          })
        else
          this.$message({
            message:'fault',
            type:"error"
          })
      })
    }
  }
}
</script>

<style scoped>

</style>



