<template>
  <el-form :rules="rules" :model="offForm">
    <el-form-item label="密码" prop="password">
      <el-input v-model="offForm.password"></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="doubleCheckPassword">
      <el-input v-model="offForm.doubleCheckPassword"></el-input>
    </el-form-item>
    <el-button @click="LogOff">注销</el-button>
  </el-form>
</template>

<script>
import axios from "axios";

export default {
  name: "logOff",
  data(){
    return{
      offForm:
      {
        password:'',
            doubleCheckPassword:''
      }
      ,
      rules:{
        password:[{required:true,trigger:'blur'}],
        doubleCheckPassword:[{required:true,trigger:'blur'}],
      }
    }
  },
  methods:{
    LogOff(url, config){
      let formData=new FormData();
      formData.append('password',this.offForm.password);
      axios.post('/logoff', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          'Token':this.$route.query.token
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



