<template>
  <el-button @click="sendToKindle">send to Kindle</el-button>
</template>

<script>
import axios from "axios";

export default {
  name: "sendToKindle",
  props:{
    bId:Number,
    token:String
  },
  methods:{
    sendToKindle(){
      axios.get('/mail/sendToKindle',{params:{bId:this.bId,Token:this.token}})
    }
  }
}
</script>

<style scoped>

</style>



