<template>
<el-table :data="bookInfoGroups">
  <el-table-column prop="bName" label="书籍名">
    <template slot-scope="scope">
      <a @click="openDetailPage(scope.row)" style="color:blue;cursor:pointer">{{ scope.row.bName }}</a>
    </template>

  </el-table-column>
  <el-table-column prop="publishingHouse" label="出版社"></el-table-column>
  <el-table-column prop="author" label="图书作者"></el-table-column>
  <el-table-column prop="yearOfPublication" label="年份"></el-table-column>
  <el-table-column prop="language" label="Chinese"></el-table-column>


</el-table>
</template>

<script>
export default {
  name: "bookInfoTable",
  props:{
    bookInfoGroups:Array
  },
  methods:{
    openDetailPage(row){
      // console.log(this.$parent.$parent.$router)
      this.$parent.$router.push({path:'/detailsOfBook',query:{bId:row.bId,token:this.$parent.$parent.token}})
    }
  }
}
</script>

<style scoped>

</style>



