<template>
  <el-table :data="category">
    <el-table-column prop="cateName" label="分类名">
      <template slot-scope="scope">
        <a @click="getBookByCategory(scope.row)" style="color:blue;cursor:pointer">{{ scope.row.cateName }}</a>
      </template>

    </el-table-column>
    <el-table-column prop="cateId" label="分类ID">

    </el-table-column>
  </el-table>
</template>

<script>
import axios from "axios";

export default {
  name: "categoryList",
  data(){
    return{
      category:[]
    }
  },
  methods:{
    getBookByCategory(row){
      this.$parent.$router.push({path:'/FirstPage',query:{cateId:row.cateId}});
      }
  },
  mounted() {
    axios.get('/cate').then(res=>{this.category=res.data})
  }
}
</script>

<style scoped>

</style>



