<template>
  <div>

    <a @click="createNewCollection" style="text-decoration: underline;cursor: pointer;">新建收藏夹</a>

    <div v-for="(collection,index) in bookCollectionList" ref="collectionList">
      <p>{{collection.cName}}</p>
      <el-button @click="checkCollection(collection.cId)">查看</el-button>
      <el-button @click="collectionDelete(collection.cId,index)">删除</el-button>
      <el-button @click="changeCollectionName(collection.cId)">改名</el-button>
      <el-button @click="cancelCollect(collection.cId,index)">取消收藏</el-button>
    </div>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "collection",
  props:{
    bookCollectionList:Array,
    token:String
  },
  methods:{
    checkCollection(cId){
      this.$parent.$router.push({path:'/FirstPage',query:{cId:cId}})
    },
    createNewCollection(){
      this.$prompt('请输入收藏夹名称','提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(({value})=>{
        let formData=new FormData();
        formData.append('cName',value);
          axios.post('/collection/addCollection',formData,{
            headers:{'Content-Type': 'multipart/form-data',
              Token:this.token
            }
          }).then(res=>{
            if(res.status>=200 && res.status<400) {


              this.$message({
                message: 'success',
                type: 'success'
              })
            }
            else
              this.$message({
                message: 'fault',
                type: 'error'
              })
          })
      })
    },
    collectionDelete(ID,index){
      axios.get('/collection/deleteCollection',{params:{cId:ID},headers:{Token:this.token}}).then(res=>{
        if(res.status>=200 && res.status<400) {

          this.$message({
            message: 'success',
            type: 'success'
          });
          this.$refs.collectionList[index].style.display='none'
        }
      })
    },
    changeCollectionName(ID){
      this.$prompt('请输入收藏夹名称','提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(({value})=> {
        axios.post('/collection/updateCollectionName',{cName:value,cId:ID},{
          headers:{Token:this.token}
        }).then(res=>{
          if(res.status>=200 && res.status<400) {

            this.$message({
              message: 'success',
              type: 'success'
            })
          }
          else
            this.$message({
              message: 'fault',
              type: 'error'
            })
        })

      })

    },
    cancelCollect(cId,index){
      axios.get('/collection/deleteCollectionOfUser',{params:{cId:cId},headers:{Token:this.token}})
      .then(res=>{
        if(res.status>=200 && res.status<400)
        {
          this.$message({
            message:'success',
            type:"success"
          })
          this.$refs.collectionList[index].style.display='none'
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



