<template>

  <el-form ref="RegisterForm" :model="RegisterForm" :rules="rules.RegisterRules" status-icon>
    <el-form-item label="用户名" prop="AccountName">
      <el-input v-model="RegisterForm.AccountName"></el-input>
    </el-form-item>
    <el-form-item label="邮箱" prop="Email">
      <el-input v-model="RegisterForm.Email"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="AccountPassWord">
      <el-input v-model="RegisterForm.AccountPassWord"></el-input>
    </el-form-item>
    <el-form-item label="验证码" prop="VerificationCode">
      <el-input v-model="RegisterForm.VerificationCode"></el-input>
    </el-form-item>
    <PostVerificationCode :email="RegisterForm.Email"></PostVerificationCode>
    <el-form-item>
      <el-button @click="Register">注册</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
import PostVerificationCode from "@/components/PostVerificationCode";
export default {
  name: "RegisterForAccount",
  data(){
    return{
      RegisterForm:{
        AccountName:'',
        AccountPassWord:'',
        Email:'',
        VerificationCode:''
      },
      rules:{
        RegisterRules:{
          AccountName:[{
            required:true,trigger:'blur'
          }],
          AccountPassWord:[
            {required:true,trigger:'blur'}
          ],
          Email:[{
            required:true,trigger:'blur'
          }],
          //todo 邮箱格式验证
          VerificationCode:[{
            required:true,trigger:'blur'
          }]
        }
      }
    }
  },
  methods:{
    async Register(){
      let formData=new FormData();
      let judgment;
      await this.$refs['RegisterForm'].validate((valid) => {
        judgment=!!valid
      });
      if(judgment) {
        formData.append('nickName', this.RegisterForm.AccountName)
        formData.append('password', this.RegisterForm.AccountPassWord)
        formData.append('email', this.RegisterForm.Email)
        formData.append('code', this.RegisterForm.VerificationCode)
        let promise = await axios.post('/register', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
      }
    }
  },
  components:{
    PostVerificationCode
  }
}

</script>

<style scoped>

</style>


