<template>
<div>
  <div>
    <h1>个人信息</h1>
    <p>email:{{user.email}}</p>
    <p>用户名:{{user.nickName}}</p>
    <p>kindleEmail:{{user.kindleEmail}}</p>
    <p>uid:{{user.uid}}</p>
  </div>

  <h2 v-if="bookList">上传的书籍</h2>
  <book-info-table v-if="bookList" :book-info-groups="bookList"></book-info-table>

  <h2 v-if="bookCollectionList">收藏夹</h2>
  <collection :book-collection-list="bookCollectionList" :token="this.$route.query.token"></collection>

<!--  <el-button @click="returnBeforePage">返回首页</el-button>-->
</div>
</template>

<script>
import axios from "axios";
import BookInfoTable from "../firstpages/bookInfoTable";
import Collection from "./collection";

export default {
  name: "mainPage",
  components: {Collection, BookInfoTable},
  methods:{
    getMainPage(){
      axios.get('/MyMainPage',{headers:{Token:this.$route.query.token.toString()}}).then(res=>{
          this.user=res.data.user;
          this.bookList=res.data.bookList;
          this.bookCollectionList=res.data.bookcollectionList;
      })
    },
    returnBeforePage(){
      this.$parent.$router.push('/')
    }
  },
  mounted() {
    this.getMainPage()
  },
  data(){
    return{
      user:{},
      bookList:[],
      bookCollectionList:[]
    }
  }
}
</script>

<style scoped>

</style>



