<template>
<el-popover trigger="click" placement="right">
  <a v-for="(collect,index) in collections" @click="addToAdoration(collect.cId)"
     style="cursor: pointer;display:block;text-decoration: underline"
  >{{collect.cName}}</a>
  <el-button slot="reference">添加至收藏</el-button>
</el-popover>
</template>

<script>
import axios from "axios";

export default {
  name: "addToAdoration",
  props:{
    collections:Array,
    bId: Number,
    token:String
  },
  methods:{
    addToAdoration(ID){
      axios.get('/collection/addBook',{headers:{Token:this.token},params:{bId:this.bId,cId:ID}})
    }
  }
}
</script>

<style scoped>

</style>



