<template>
  <div class="comments">
    评论区:
    <div v-for="(comment,index) in commentList" class="oneInComments" ref="commentList">
      <h2>{{comment.comContent}}</h2>
      <p>作者:{{comment.uId}}</p>
      <p>like:{{comment.comLike}}</p>
      <p>dislike:{{comment.comDislike}}</p>
      <i class="el-icon-success" @click="like(index,comment.comId)" style="cursor: pointer">赞</i>
      <i class="el-icon-error" @click="dislike(index,comment.comId)" style="cursor: pointer">踩</i>
      <el-button @click="deleteComment(comment.comId,index)">删除</el-button>
    </div>


  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "commentList",
  props:{
    commentList:Array,
    token:String
  },
  methods:{
    like(index,comId){
      axios.post('/comment/like',{
        userLike:{comId:comId,action:1},
        isCancel:this.isCancelOfLike[index]
      },{headers: {Token: this.token}}).then(res=>{
        if(res.status>=200 && res.status<400) {
          this.isCancelOfLike[index] = !this.isCancelOfLike[index]
          this.$message({
            message:'success'
          })
        }
      })
    },
    dislike(index,comId){
      axios.post('/comment/dislike',{
        userLike:{comId:comId,action:-1},
        isCancel:this.isCancelOfDislike[index]
      },{headers: {Token: this.token}}).then(res=>{
        if(res.status>=200 && res.status<400) {
          this.isCancelOfDislike[index] = !this.isCancelOfDislike[index];
          this.$message({
            message:'success'
          })
        }
      })
    },
    deleteComment(comId,index){
      axios.get('/comment/delete',{params:{comId:comId,uId:this.commentList[index.uId],bId:this.commentList[index.bId]},headers:{Token:this.token}}).then(res=>{
        if(res.status>=200 && res.status<400){
          this.$message({
            message:'success'
          })
          this.$refs.commentList[index].style.display='none'
        }
      })
    }
  },
  data(){
    return{
      isCancelOfLike:[],
      isCancelOfDislike:[]
    }
  },
  mounted() {
    this.commentList.forEach(()=>{
      this.isCancelOfLike.push(false);
      this.isCancelOfDislike.push(false);
    })
  }
}
</script>

<style scoped>

  .oneInComments{
    border-bottom: #3a8ee6 solid;
  }
</style>



