<template>
  <el-row>
    <el-col :span="6" :offset="9">
      <el-input
          type="text"
          prefix-icon="el-icon-search"
          v-model="keyword"
          placeholder="支持“喜欢的用户名”、“ISBN”索书号搜索"
          style="cursor: pointer"
          @keyup.enter.native="getAllKb"
      ></el-input>
    </el-col>
  </el-row>

</template>

<script>

import axios from "axios";

export default {
  name: 'searchBox',
  components: {
  },
  data(){
    return{
      keyword:'',
    }
  },
  methods:{
    async getAllKb(){
      let promise=await axios.get('/search',{params:{keyword:this.keyword}})
      if(promise.status>=200 && promise.status<400){
        this.$message({
          message:'success',
          type:"success"
        });
        this.getData(promise.data);
      }
      this.keyword=''
    }
  },
  props:{
    getData:{
      type:Function
    }
  }
}
</script>

<style >
#app {

}
</style>



