<template>
  <el-form ref="ChangePasswordForm" :model="ChangePasswordForm" :rules="rules.ChangePassword" status-icon>
    <el-form-item label="邮箱" prop="Email">
      <el-input v-model="ChangePasswordForm.Email"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="AccountPassWord">
      <el-input type="password" v-model="ChangePasswordForm.AccountPassWord"></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="DoublePassWord">
      <el-input type="password" v-model="ChangePasswordForm.DoublePassWord"></el-input>
    </el-form-item>
    <el-form-item label="验证码" prop="VerificationCode">
      <el-input v-model="ChangePasswordForm.VerificationCode"></el-input>
    </el-form-item>
    <PostVerificationCode :email="ChangePasswordForm.Email"></PostVerificationCode>
    <el-form-item>
      <el-button @click="ChangePassword">确认</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
import PostVerificationCode from "@/components/PostVerificationCode";
export default {
  name: "ChangePassword",
  methods:{
    ChangePassword(){
      let formData=new FormData();
      formData.append('mail',this.ChangePasswordForm.mail)
      formData.append('code',this.ChangePasswordForm.VerificationCode)
      formData.append('newPassword',this.ChangePasswordForm.AccountPassWord)
      axios.post('/changePassword',formData,{headers:{'Content-Type': 'multipart/form-data'}}).then((res)=>{
        if(res.status>=200 && res.status<400) {

          this.$message({
            message:'success',
            type:'success'
          })

        }
      })
    }
  },
  data(){
    let DoubleCheck=(rule,value,callback)=>{
      if(value === this.ChangePasswordForm.AccountPassWord)
        callback();
      else
      callback(new Error('两次密码不一致'))
    }
    return{
      ChangePasswordForm:{
        Email:'',
        AccountPassWord:'',
        DoublePassWord:'',
        VerificationCode:''
      },
      rules:{
        ChangePassword:{
          Email:[{required:true,trigger:'blur'}],
          AccountPassWord:[{required:true,trigger:'blur'}],
          DoublePassWord:[{validator:DoubleCheck,required:true,trigger:'blur'}],
          VerificationCode:[{required:true,trigger:'blur'}]
        }
      }

    }
  },
  components:{
    PostVerificationCode
  }
}
</script>

<style scoped>

</style>



