<template>
<el-button @click="LogOut">下线</el-button>
</template>

<script>
import axios from "axios";

export default {
  name: "logOut",

  methods:{
    LogOut(){
      axios.get('/logout',{headers:{
        'Token':this.$route.query.token
        }})
    }
  }
}
</script>

<style scoped>

</style>



