<template>
  <div>
    <searchBox :get-data="getData"></searchBox>
    <book-info-table :book-info-groups="bookInfoGroups"></book-info-table>
  </div>
</template>

<script>
import SearchBox from "./searchBox";
import BookInfoTable from "./bookInfoTable";
import axios from "axios";
export default {
  name: "FirstPage",
  components: {BookInfoTable, SearchBox},
  data(){
    return{
      bookInfoGroups:[]
    }
  },
  methods:{
    getData(data){
      this.bookInfoGroups=data;
    },
    getBookByAuthor(author){
      axios.get('/getBookByAuthor',{params:{author:author}}).then(res=>{
        this.bookInfoGroups=res.data;
      })
    },
    getBookByCate(cateId){
      axios.get('/getBookByCate',{params:{cateId:cateId}}).then(res=>{
        this.bookInfoGroups=res.data;
      })

    },
    getBookByCollection(cId){
      axios.get('/collection/getBook',{params:{cId:cId}}).then(res=>{
        this.bookInfoGroups=res.data;
      })
    }
  },
  mounted() {
    if(this.$route.query.author)
      this.getBookByAuthor(this.$route.query.author);
    if(this.$route.query.cateId)
      this.getBookByCate(this.$route.query.cateId)
    if(this.$route.query.cId)
      this.getBookByCollection(this.$route.query.cId)

  }
}
</script>

<style scoped>

</style>



