<template>
<div>
  <h1>{{book.book.bName}}</h1>
  <p>简介：{{book.book.description}}</p>
  <a>作者：<a style="text-decoration: underline ;cursor: pointer" @click="getBookByAuthor(book.book.author)">{{book.book.author}}</a></a>
  <p v-for="(category,index) in book.categoryList">分类：<a style="text-decoration: underline ;cursor: pointer" @click="getBookByCate(category.cateId)">{{category.cateName}}</a></p>
  <p>language:{{book.book.language}}</p>
  <p>ISBN:{{book.book.ISBN}}</p>
  <p>出版年份:{{book.book.yearOfPublication}}</p>
  <p>出版社:{{book.book.publishingHouse}}</p>
  <book-download :uri="book.book.uri"></book-download>
  <send-to-kindle :b-id="this.$route.query.bId" :token="this.$route.query.token"></send-to-kindle>
  <add-to-adoration :collections="this.$parent.collections" :b-id="this.$route.query.bId" :token="this.$route.query.token"></add-to-adoration>
  <comment-list :comment-list="commentList" :token="this.$route.query.token"></comment-list>
  <post-comment :b-id="this.$route.query.bId" :token="this.$route.query.token"></post-comment>
</div>
</template>

<script>
import axios from "axios";
import BookDownload from "./bookDownload";
import SendToKindle from "./sendToKindle";
import AddToAdoration from "./addToAdoration";
import CommentList from "../comment/commentList";
import PostComment from "../comment/postComment";

export default {
  name: "detailsOfBook",
  components: {PostComment, CommentList, AddToAdoration, SendToKindle, BookDownload},
  methods:{
    getDetailsOfBook(){
      axios.get('/getbook',{params:{bId:this.$route.query.bId}}).then(res=>{
        this.book=res.data
      })

    },
    getComments(){
      axios.get('/comment/get',{params:{bId:this.$route.query.bId}}).then(res=>{
        this.commentList=res.data;
      })
    }
    ,
    getBookByAuthor(author){
      this.$parent.$router.push({path:'/FirstPage',query:{author:author}});
    },
    getBookByCate(cateId){
      this.$parent.$router.push({path:'/FirstPage',query:{cateId:cateId}});
    }
  },
  mounted() {
    this.getDetailsOfBook()
    this.getComments()
  },
  data(){
    return{
      book: {},
      commentList:[]
    }
  }
}
</script>

<style scoped>

</style>



