<template>
  <el-form :rules="rules" :model="SetForm">
    <el-form-item label="kindleEmail" prop="kindleEmail">
      <el-input v-model="SetForm.kindleEmail"></el-input>
    </el-form-item>
    <el-form-item label="确认kindleEmail" prop="doubleCheckKindleEmail">
      <el-input v-model="SetForm.doubleCheckKindleEmail"></el-input>
    </el-form-item>
    <el-button @click="SetEmail">确定</el-button>
  </el-form>
</template>

<script>
import axios from "axios";

export default {
  name: "setKindleEmail",
  data(){
    return{
      SetForm:
          {
            kindleEmail:'',
            doubleCheckKindleEmail:''
          }
      ,
      rules:{
        kindleEmail:[{required:true,trigger:'blur'}],
        doubleCheckKindleEmail:[{required:true,trigger:'blur'}],
      }
    }
  },

  methods:{
    SetEmail(url, config){
      let formData=new FormData();
      formData.append('KindleEmail',this.SetForm.kindleEmail);
      axios.post('/mail/setKindleEmail', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          'Token':this.$route.query.token
        }
      })
    }
  }

}
</script>

<style scoped>

</style>



