<template>
  <el-button @click="PostVerificationCode">获取验证码</el-button>
</template>

<script>
import axios from "axios";

export default {
  name: "PostVerificationCode",
  methods:{
    PostVerificationCode(){
      if(this.email.search("@") !== -1 && this.email.search(".com") !== -1)
        {
           axios.get('/mail/sendMail',{params:{
              mail:this.email
            }}).then((res)=>{
             if( res.status >= 200 && res.status < 400){
               this.$message({
                 message:'请检查邮箱',
                 type:'success'
               })
             }
             else
               this.$message(
                   {
                     message:'error',
                     type:'error'
                   }
               )
           })
        }
      else
        this.$message(
            {
              message:'请检查email格式',
              type:'error'
            }
        )
    }

  },
  props:{
    email:String
  }
}
</script>

<style scoped>

</style>


