<template>
  <div>
    <el-row>
      <el-col>
        <div style="height: 40px">
          <el-button type="primary" @click="backToFirst">图书馆主页</el-button>
          <el-menu  mode="horizontal" @select="handleSelect">
            <el-submenu index="1" >
            <template slot="title">个人</template>
            <el-menu-item index="1-1" @click="toLogIn" v-if="!this.token">登录</el-menu-item>
            <el-menu-item index="1-7" @click="toMainPage" v-if="this.token">个人主页</el-menu-item>
            <el-menu-item index="1-2" @click="toRegister">注册</el-menu-item>
            <el-menu-item index="1-3" @click="toLogOut" v-if="this.token">登出</el-menu-item>
            <el-menu-item index="1-4" @click="toLogOff" v-if="this.token">注销</el-menu-item>
            <el-menu-item index="1-5" @click="setKindleEmail" v-if="this.token">设置kindle邮箱</el-menu-item>
            <el-menu-item index="1-6" @click="registerAsManagement">管理员注册</el-menu-item>
            <el-menu-item index="1-8" @click="toChangePassword" v-if="this.token">改密码</el-menu-item>

            </el-submenu>
            <el-submenu index="2">
              <template slot="title">书籍</template>
              <el-menu-item index="2-1" @click="toCategory">分类</el-menu-item>
            </el-submenu>
          </el-menu>
        </div>
      </el-col>
    </el-row>
    <el-button @click="getToken">token</el-button>
    <el-button @click="getCollections">collections</el-button>
    <router-view></router-view>

<!--    <searchBox :get-data="getData"></searchBox>-->
<!--    <book-info-table :book-info-groups="bookInfoGroups"></book-info-table>-->

  </div>

</template>

<script>
import searchBox from './firstpages/searchBox'
import bookInfoTable from "./firstpages/bookInfoTable";
import axios from "axios";
export default {
  name: "App",
  components:{
    searchBox,
    bookInfoTable
  },
  methods:{
    getData(data){
      this.bookInfoGroups=data;
    },
    output(){
      console.log(this.bookInfoGroups)
    },
    backToFirst(){
      this.$router.push({path:'/FirstPage'})
    },
    handleSelect(key, keyPath){
      console.log(key, keyPath);
    },
    toLogIn(){
      this.$router.push({path:'/LogInForAccount'})
    },
    toRegister(){
      this.$router.push({path:'/RegisterForAccount'})
    },
    toLogOff(){
      this.$router.push({path:'/logOff',query:{token:this.token}})
    },
    toLogOut(){
      this.$router.push({path:'/logOut',query:{token:this.token}})
    },
    setKindleEmail(){
      this.$router.push({path:'/setKindleEmail',query:{token:this.token}})
    },
    registerAsManagement(){
      this.$router.push({path:'/registerAsManagement'})
    },
    getToken(){
      console.log(this.token)
    },
    getCollections(){
      console.log(this.collections)
    },
    toMainPage(){
      this.$router.push({path:'/mainPage',query:{token:this.token}})
    },
    toCategory(){
      this.$router.push({path:'/categoryList'})
    },
    toChangePassword(){
      this.$router.push({path:'/ChangePassword',query:{token:this.token}})
    }
  },
  data(){
    return{
      bookInfoGroups:[],
      token:'',
      collections:[]
    }
  },

}
</script>

<style scoped>

</style>



