import Vue from "vue";
import VueRouter from "vue-router";
import ChangePassword from "../components/ChangePassword";
import mainPage from "../components/personalPage/mainPage";
import detailsOfBook from "../components/book/detailsOfBook";
import FirstPage from "../components/firstpages/FirstPage";
import LogInForAccount from "../components/account/LogInForAccount";
import RegisterForAccount from "../components/RegisterForAccount";
import logOff from "../components/account/logOff";
import setKindleEmail from "../components/account/setKindleEmail";
import logOut from "../components/account/logOut";
import registerAsManagement from "../components/account/registerAsManagement";
import categoryList from "../components/firstpages/categoryList";

Vue.use(VueRouter);

const routes=[
  {
    path: '/',
    component:FirstPage ,
    hidden:true
  },
  {
    path: '/FirstPage',
    component:FirstPage ,
    hidden:true
  },
  {
    path:'/ChangePassword',
    component: ChangePassword,
    hidden:true
  },

  {
    path:'/mainPage',
    component: mainPage,
    hidden:true
  },
  {
    path:'/detailsOfBook',
    component: detailsOfBook,
    hidden:true
  },
  {
    path:'/LogInForAccount',
    component: LogInForAccount,
    hidden:true
  },  {
    path:'/RegisterForAccount',
    component: RegisterForAccount,
    hidden:true
  }, {
    path:'/logOff',
    component: logOff,
    hidden:true
  },{
    path:'/setKindleEmail',
    component: setKindleEmail,
    hidden:true
  },{
    path:'/logOut',
    component: logOut,
    hidden:true
  },{
    path:'/registerAsManagement',
    component: registerAsManagement,
    hidden:true
  },{
    path:'/categoryList',
    component: categoryList,
    hidden:true
  },
]

export default new VueRouter({
    routes
})



