<template>

  <el-form ref="LogInForm" :model="LogInForm" :rules="rules.LogInRules" status-icon>
    <el-form-item label="邮箱" prop="email">
      <el-input v-model="LogInForm.email"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="AccountPassWord">
      <el-input v-model="LogInForm.AccountPassWord"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button @click="LogIn">登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";

export default {
  name: "LogInForAccount",
  data(){
    return{
      LogInForm:{
        email:'',
        AccountPassWord:''
      },
      rules:{
        LogInRules:{
          email:[{
            required:true,trigger:'blur'
          }],
          AccountPassWord:[
            {required:true,trigger:'blur'}
          ]
        }
      }
    }
  },
  methods:{
    async LogIn(){
      let formData=new FormData();
      let judgment;
      await this.$refs['LogInForm'].validate((valid) => {
        judgment=!!valid
      });
      if(judgment) {
        formData.append('email', this.LogInForm.email)
        formData.append('password', this.LogInForm.AccountPassWord)
        axios.post('/login', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then((res)=>{
          if(res.status>=200 && res.status<400) {

            this.$parent.token = res.data;
            this.$message({
              message:'success',
              type:'success'
            })
            this.getCollections();
          }
        })
      }
    },
    getCollections(){
      axios.get('/collection/getAllCollection',{headers:{Token:this.$parent.token}}).then(res=>{this.$parent.collections=res.data;
        console.log(res.data)})
    }
  }
}

</script>

<style scoped>

</style>



